"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Logo } from "@/components/logo";
import { Download } from "lucide-react";

export default function GuidePage() {
  const [step, setStep] = useState(0);
  const [email, setEmail] = useState("");
  const [formData, setFormData] = useState({
    first: "",
    last: "",
    phone: "",
    website: "",
    revenue: "",
    constraint: ""
  });
  const [error, setError] = useState("");

  const validateEmail = (email: string) => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };

  const handleEmailSubmit = () => {
    setError("");
    if (!email.trim()) {
      return setError("Please enter your email address");
    }
    if (!validateEmail(email)) {
      return setError("Please enter a valid email address");
    }
    setStep(1);
  };

  const handleFormSubmit = async () => {
    setError("");
    if (!formData.first || !formData.last || !formData.phone || !formData.website || !formData.revenue || !formData.constraint) {
      return setError("Please fill in all fields");
    }

    // Send data to webhook
    try {
      await fetch("https://hook.eu2.make.com/xq1dvs5e98p1w88grh58skodmco254tx", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          email,
          formData,
          type: "guide-download",
          completedAt: new Date().toISOString(),
        }),
      });
    } catch (e) {
      // Fail silently
    }

    // Trigger download (you can replace this with actual download logic)
    window.open("/path-to-your-guide.pdf", "_blank");
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      if (step === 0) {
        handleEmailSubmit();
      } else {
        handleFormSubmit();
      }
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header with Logo */}
      <header className="py-6 px-4">
        <div className="max-w-2xl mx-auto flex justify-center">
          <Logo />
        </div>
      </header>

      <div className="max-w-2xl mx-auto px-4 py-8">
        {/* Hero Section */}
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6">
            Download Our 12 Free B2B Growth Trainings
          </h1>
          <p className="text-xl text-gray-600 leading-relaxed">
            Get instant access to our trainings on how to build a top 0.1% B2B client acquisition funnel.
          </p>
        </div>

        {/* Step 1: Email Collection */}
        {step === 0 && (
          <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-8">
            <div className="text-center mb-8">
              <h2 className="text-2xl font-semibold text-gray-900 mb-2">Get Your Free Trainings</h2>
              <p className="text-gray-600">Enter your email to get instant access</p>
            </div>
            
            <div className="space-y-4">
              <Input
                type="email"
                placeholder="Enter your email address"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                onKeyPress={handleKeyPress}
                className="text-lg p-4 focus:ring-2 focus:ring-[#A8FF9E]"
              />
              
              {error && <p className="text-red-500 text-sm text-center">{error}</p>}
              
              <Button 
                onClick={handleEmailSubmit}
                className="w-full text-lg py-4 bg-[#A8FF9E] text-black hover:bg-[#A8FF9E]/90"
              >
                <Download className="mr-2 h-5 w-5" />
                Get Free Trainings
              </Button>
            </div>
          </div>
        )}

        {/* Step 2: Detailed Form */}
        {step === 1 && (
          <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-8">
            <div className="text-center mb-8">
              <h2 className="text-2xl font-semibold text-gray-900 mb-2">Almost there!</h2>
              <p className="text-gray-600">Help us personalize your experience</p>
            </div>
            
            <div className="space-y-4">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div>
                  <Label className="text-sm font-medium text-gray-700">First name</Label>
                  <Input
                    placeholder="Enter your first name"
                    value={formData.first}
                    onChange={(e) => setFormData({ ...formData, first: e.target.value })}
                    onKeyPress={handleKeyPress}
                    className="mt-1 p-4 focus:ring-2 focus:ring-[#A8FF9E]"
                  />
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-700">Last name</Label>
                  <Input
                    placeholder="Enter your last name"
                    value={formData.last}
                    onChange={(e) => setFormData({ ...formData, last: e.target.value })}
                    onKeyPress={handleKeyPress}
                    className="mt-1 p-4 focus:ring-2 focus:ring-[#A8FF9E]"
                  />
                </div>
              </div>
              
              <div>
                <Label className="text-sm font-medium text-gray-700">Phone number</Label>
                <Input
                  placeholder="Enter your phone number"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  onKeyPress={handleKeyPress}
                  className="mt-1 p-4 focus:ring-2 focus:ring-[#A8FF9E]"
                />
              </div>
              
              <div>
                <Label className="text-sm font-medium text-gray-700">Website</Label>
                <Input
                  placeholder="Enter your website URL"
                  value={formData.website}
                  onChange={(e) => setFormData({ ...formData, website: e.target.value })}
                  onKeyPress={handleKeyPress}
                  className="mt-1 p-4 focus:ring-2 focus:ring-[#A8FF9E]"
                />
              </div>
              
              <div>
                <Label className="text-sm font-medium text-gray-700">Annual revenue</Label>
                <select
                  value={formData.revenue}
                  onChange={(e) => setFormData({ ...formData, revenue: e.target.value })}
                  className="mt-1 w-full p-4 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#A8FF9E] focus:border-transparent"
                >
                  <option value="">Select your annual revenue</option>
                  <option value="under-100k">Under $100k</option>
                  <option value="100k-500k">$100k - $500k</option>
                  <option value="500k-1m">$500k - $1M</option>
                  <option value="1m-5m">$1M - $5M</option>
                  <option value="5m-10m">$5M - $10M</option>
                  <option value="10m+">$10M+</option>
                </select>
              </div>
              
              <div>
                <Label className="text-sm font-medium text-gray-700">Biggest growth constraint</Label>
                <select
                  value={formData.constraint}
                  onChange={(e) => setFormData({ ...formData, constraint: e.target.value })}
                  className="mt-1 w-full p-4 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#A8FF9E] focus:border-transparent"
                >
                  <option value="">Select your biggest constraint</option>
                  <option value="lead-generation">Lead Generation</option>
                  <option value="conversion-rate">Conversion Rate</option>
                  <option value="sales-process">Sales Process</option>
                  <option value="team-scaling">Team Scaling</option>
                  <option value="marketing-budget">Marketing Budget</option>
                  <option value="brand-awareness">Brand Awareness</option>
                  <option value="other">Other</option>
                </select>
              </div>
              
              {error && <p className="text-red-500 text-sm text-center">{error}</p>}
              
              <Button 
                onClick={handleFormSubmit}
                className="w-full text-lg py-4 bg-[#A8FF9E] text-black hover:bg-[#A8FF9E]/90"
              >
                <Download className="mr-2 h-5 w-5" />
                Download Trainings
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
} 